import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/page/index'
import Content from '@/page/content'

Vue.use(Router)

export default new Router({
	mode: "history",
	base: '/showapp/',
	routes: [{
		path: '/',
		component: Index
	},{
		path: '/create',
		component: Index
	}, {
		path: '/content/:id',
		component: Content
	}]
})


